.shopcategory-banner {
  display: block;
  width: 82%;
  margin: 30px auto;
}

.shopcategory-indexsort {
  display: flex;
  margin: 0px 170px;
  align-items: center;
  justify-content: space-between;
}

.shopcategory-sort {
  padding: 10px 20px;
  border-radius: 40px;
  border: 1px solid #888;
}

.shopcategory-indexsort p span {
  font-weight: 600;
}

.shopcategory-products {
  margin: 20px 170px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 80px;
}


.shopcategory-loadmore{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 233px;
    height: 63px;
    margin: 100px auto;
    border-radius: 75px;
    background: #ededed;
    color: #787878;
    font-size: 18px;
    font-weight: 500;
}